<template>
  <div class="passreset__screen">
    <div
      class="
        resetpass
        p-10
        flex-col
        rounded-sm
        items-center
        justify-center
        w-full
        m-2
        md:w-2/3
      "
    >
      <img
        class="forgetpass__image mx-auto mb-2"
        src="@/assets/resetpass.svg"
        alt=""
      />

      <input class="input-block" placeholder="E-mail" v-model.trim="email" />
      <div dark v-if="error" class="bg-red-500 p-4 mt-2 rounded text-white">
        {{ error }}
      </div>
      <div class="text-center">
        <button
          class="mx-auto primary_btn mt-4 border border-white"
          @click="passwordReset"
        >
          Send Confirmation Email
        </button>
      </div>

      <div class="mt-5 text-center">
        <router-link
          to="/login"
          class="
            mx-auto
            px-3
            py-2
            rounded
            text-white
            font-semibold
            bg-blue-500
            border border-white
          "
          >Go to login page.
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { auth } from "@/plugins/firebase";
export default {
  data() {
    return {
      email: "",
      error: "",
    };
  },
  methods: {
    passwordReset() {
      if (this.email) {
        auth
          .sendPasswordResetEmail(this.email)
          .then(() => {
            this.showAlert();
            this.email = "";
          })
          .catch((error) => {
            this.error = error.message;
          });
      }
    },
    showAlert() {
      this.$swal("Email sent!", "Check your inbox and spam.", "success");
    },
  },
};
</script>

